// ** React Imports
import { useContext, useEffect, useState } from 'react'

// ** Third Party Components
import ReactCountryFlag from 'react-country-flag'
import { UncontrolledDropdown, DropdownMenu, DropdownItem, DropdownToggle } from 'reactstrap'

// ** Internationalization Context
import { IntlContext } from '@src/utility/context/Internationalization'
import { FormattedMessage } from 'react-intl'

const IntlDropdown = () => {
  // ** Context
  const intlContext = useContext(IntlContext)
  const [langObj,setLangObj] = useState({
    en: <FormattedMessage id='English' />,
        jp: <FormattedMessage id='Japanese' />,
        vn: <FormattedMessage id='Vietnamese' />
  })
  // ** Function to switch Language
  const handleLangUpdate = (e, lang) => {
    // localStorage.setItem('lang', lang)
    localStorage.setItem('language', lang)
    e.preventDefault()
    intlContext.switchLanguage(lang)
  }

  useEffect(()=>{
    intlContext.switchLanguage(localStorage.getItem('language') || 'vn')

  },[])

  return (
    <UncontrolledDropdown href='/' tag='li' className='dropdown-language nav-item'>
      <DropdownToggle href='/' tag='a' className='nav-link' onClick={e => e.preventDefault()}>
        <ReactCountryFlag
          className='country-flag flag-icon'
          countryCode={intlContext.locale === 'en' ? 'us' : intlContext.locale}
          svg
        />
        <span className='selected-language'>{langObj[intlContext.locale]}</span>
      </DropdownToggle>
      <DropdownMenu className='mt-0' right>
        <DropdownItem href='/' tag='b' onClick={e => handleLangUpdate(e, 'en')}>
          <ReactCountryFlag className='country-flag' countryCode='us' svg />
          <span className='ml-1'><FormattedMessage id='English' /></span>
        </DropdownItem>
        <DropdownItem href='/' tag='c' onClick={e => handleLangUpdate(e, 'jp')}>
          <ReactCountryFlag className='country-flag' countryCode='jp' svg />
          <span className='ml-1'><FormattedMessage id='Japanese' /></span>
        </DropdownItem>
        <DropdownItem href='/' tag='a' onClick={e => handleLangUpdate(e, 'vn')}>
          <ReactCountryFlag className='country-flag' countryCode='vn' svg />
          <span className='ml-1'> <FormattedMessage id='Vietnamese' /></span>
        </DropdownItem>


      </DropdownMenu>
    </UncontrolledDropdown>
  )
}

export default IntlDropdown




